import BackgroundLogo from "../../assets/images/BackgroundLogo.gif";
import React, {useEffect, useState} from "react";
import {CCard, CCardBody, CRow} from "@coreui/react";
import Form from "react-bootstrap/Form";
import Select from 'react-select';
import Button from "react-bootstrap/Button";
import {errorToast, getCookieValue, ipAddress, successToast, tokenName} from "../../utils/Common";
import axios from "axios";
import {useForm} from "react-hook-form";
import Multiselect from 'multiselect-react-dropdown';

const optionsFareType = [
  {value: 'All', label: 'All'},
  {value: 'Saver', label: 'Saver'},
  {value: 'Corp_Connect', label: 'Corp Connect'},
  {value: 'Flexi_Fare', label: 'Flexi Fare'},
  {value: 'Coupon_Fare', label: 'Coupon Fare'},
  {value: 'Super6E', label: 'Super6E'},
  {value: 'SME_Fare', label: 'SME Fare'},
  {value: 'InstaPur_Fare', label: 'InstaPur Fare'},
];

const optionsCarrierList = [
  {value: 'All', label: 'All'},
];

const optionsSupplierList = [
  {value: 'All', label: 'All'},
  {value: 'TBO', label: 'TBO'},
  {value: 'Tripjack', label: 'Tripjack'}
];

const FlightsMarkup = () => {

  useEffect(() => {
    showCarriersList();
    getMarkupData();
  }, []);

  const [selectedOptionFareType, setSelectedOptionFareType] = useState([]);
  const [selectedOptionCarrierList, setSelectedOptionCarrierList] = useState([]);
  const [selectedOptionSupplierList, setSelectedOptionSupplierList] = useState([]);
  const [carriersList, setCarriersList] = useState([]);
  let {register, handleSubmit, reset, formState: {errors}} = useForm();

  function showCarriersList() {
    let token = getCookieValue(tokenName);
    // console.log(token)
    axios.get(ipAddress + "admin/airlines", {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    })
      .then((response) => {
        const airlines = response.data.recordset.map(airline => ({
          value: airline.Code,
          label: airline.Name
        }));
        setCarriersList(airlines);
      }).catch(e => {
      console.log(e)
    })
  }

  function onSubmit(Params) {

    Params.fareType = selectedOptionFareType;
    Params.carrierList = selectedOptionCarrierList;
    Params.supplierList = selectedOptionSupplierList;

    // console.log(Params)
    //
    // let supplierListLength = Params.supplierList.length
    // for (let i = 0; i < supplierListLength; i++) {
    //   let supplierName = Params.supplierList[i].value;
    //
    //   for (let c of Params.carrierList) {
    //     let code = c.value;
    //     let carrierName = c.label;
    //
    //     for (let f of Params.fareType) {
    //       let fare = f.value;
    //       console.log(supplierName, code, carrierName, fare)
    //     }
    //   }
    //
    //   if (i === (supplierListLength - 1)) {
    //     console.log("equal")
    //   }
    // }

    if(Params.carrierList.length === 0)
    {
      errorToast("Choose Carriers First");
    } else if(Params.fareType.length === 0)
    {
      errorToast("Choose fare Type")
    }else if(Params.supplierList.length === 0)
    {
      errorToast("Choose Supplier First");
    }else {
      let token = getCookieValue(tokenName);
      // console.log(token)
      axios.post(ipAddress + "admin/markupPlans", Params, {
        headers: {
          'Authorization': `Bearer ${token}`,
          'Content_Type': 'application-json',
        }
      })
        .then((response) => {
          console.log(response.data);
          if (response.data.error === false) {
            reset();
            setSelectedOptionSupplierList([]);
            setSelectedOptionFareType([]);
            setSelectedOptionCarrierList([]);
            successToast(response.data.message);
          } else {
            errorToast(response.data.message);
          }
        }).catch(e => {
        console.log(e)
      })
    }
  }

  function getMarkupData() {
    let token = getCookieValue(tokenName);
    // console.log(token)
    axios.get(ipAddress + "admin/markupPlans", {
      headers: {
        'Authorization': `Bearer ${token}`,
        'Content_Type': 'application-json',
      }
    }).then((response) => {
      console.log(response);
    }).catch(e => {
      console.log(e)

    })
  }

  return (
    <>
      {/*<div style={{*/}
      {/*  backgroundImage: `url(${BackgroundLogo})`,*/}
      {/*  backgroundRepeat: 'no-repeat',*/}
      {/*  backgroundSize: '100% 100%',*/}
      {/*  position: "relative",*/}
      {/*  width: '75vw',*/}
      {/*  height: '70vh',*/}
      {/*  backgroundPosition: "center",*/}
      {/*  opacity: "0.3"*/}
      {/*}}>*/}
      {/*</div>*/}

      <CCard className="mb-4">
        <CCardBody>
          <CRow>
            <form onSubmit={handleSubmit(onSubmit)}>
              <div className="table-responsive px-3">
                <div className="row bg-light py-2 mb-3">
                  <div className="text-center">
                    <h2><b>Add New Markup</b></h2>
                    <div className="row">
                      <div className="col-sm-4"></div>
                      <div className="col-sm-4">
                        <hr style={{color: "blue", border: "2px solid"}}/>
                      </div>
                      <div className="col-sm-4"></div>
                    </div>
                  </div>
                  <br/>
                  <br/>
                  <br/>
                  <br/>


                  <div className="col-6 col-md-4">
                    <span><b>Plan Name</b></span>
                    <input {...register('plan_name', {required: true})} type="text" className="form-control"/>
                    {errors.plan_name && <p className='text-danger'>This is a required field.</p>}
                  </div>
                  <div className="col-6 col-md-4 ">
                    <span><b>Type</b></span>
                    <select {...register('type', {required: true})} className="form-control" name="type" id="type">
                      <option value="">Choose Markup Type</option>
                      <option value="Fixed">Fixed</option>
                      <option value="Percentage">Percentage</option>
                    </select>
                  </div>
                  <div className="col-6 col-md-4">
                    <span><b>Markup Value</b></span>
                    <input {...register('value', {required: true})} type="text" pattern="[0-9]{1,9}"
                           className="form-control"/>
                    {errors.value && <p className='text-danger'>This is a required field.</p>}
                  </div>

                  <div style={{marginTop: "12px"}} className="col-6 col-md-4">
                    <span><b>Supplier List</b></span>

                    <Select
                      value={selectedOptionSupplierList}
                      onChange={setSelectedOptionSupplierList}
                      options={optionsSupplierList}
                      isMulti
                    />

                  </div>

                  <div style={{marginTop: "12px"}} className="col-6 col-md-4">
                    <span><b>Fare Type</b></span>

                    <Select
                      value={selectedOptionFareType}
                      onChange={setSelectedOptionFareType}
                      options={optionsFareType}
                      isMulti
                      menuPortalTarget={document.body}
                      styles={{
                        menuPortal: base => ({...base, zIndex: 9999}),
                        menu: base => ({...base, maxHeight: 200, overflowY: 'auto',}) // Adjust the max height as needed
                      }}
                    />

                  </div>

                  <div style={{marginTop: "12px"}} className="col-6 col-md-4">
                    <span><b>Carrier List</b></span>

                    <Select
                      value={selectedOptionCarrierList}
                      onChange={setSelectedOptionCarrierList}
                      options={[{value: 'All', label: 'All'}, ...carriersList]}
                      isMulti
                      menuPortalTarget={document.body}
                      styles={{
                        menuPortal: base => ({...base, zIndex: 9999}),
                        menu: base => ({...base, maxHeight: 200, overflowY: 'auto',}) // Adjust the max height as needed
                      }}
                    />

                  </div>


                  <div style={{marginTop: "12px"}} className="col-6 col-md-4">
                    <span><b>Cancellation Charges</b></span>
                    <input {...register('cancellation', {required: true})} type="text" pattern="[0-9]{1,9}"
                           className="form-control"/>
                    {errors.cancellation && <p className='text-danger'>This is a required field.</p>}
                  </div>

                  <div style={{marginTop: "12px"}} className="col-6 col-md-4">
                    <span><b>Rescheduling Charges</b></span>
                    <input {...register('rescheduling', {required: true})} type="text" pattern="[0-9]{1,9}"
                           className="form-control"/>
                    {errors.rescheduling && <p className='text-danger'>This is a required field.</p>}
                  </div>

                  <div className="col-12" style={{marginTop: "12px"}}>
                    <Button type="submit" variant="primary">
                      Add New Markup
                    </Button>

                  </div>

                </div>
              </div>
            </form>
          </CRow>
        </CCardBody>
      </CCard>

    </>
  )
}

export default FlightsMarkup
